import { writable } from "svelte/store";
import { Creditos, Dinheiro, Fome, ForcaFisica, Sede, Sono } from "../interfaces/brazucas";
import Swal from 'sweetalert2';
import { BehaviorSubject } from "rxjs";
export const playerGuiMenuAtivo = new BehaviorSubject(false);
export const voiceChatListeners = writable([]);
export const jogador = writable({
    nome: 'Carregando...',
    email: '',
    fome: Fome.MAXIMO,
    sono: Sono.MAXIMO,
    forcaFisica: ForcaFisica.MAXIMO,
    sede: Sede.MAXIMO,
    dinheiro: Dinheiro.MINIMO,
    creditos: Creditos.MINIMO,
});
export const mostrarErro = (mensagem) => Swal.fire({
    title: 'Oops!',
    text: obterMensagemDeErro(mensagem),
    icon: 'error',
    confirmButtonText: 'Fechar',
});
const obterMensagemDeErro = (e) => {
    return 'Um erro ocorreu ao processar sua solicitação, tente novamente mais tarde.';
};
